<template>
  <div class="main-container">
    <div class="row">
      <div class="col-md-4">
      </div>
      <div class="col-md-4">
        <form class="form-horizontal">
          <div class="form-title">
            <h5>{{$t("登 录")}}</h5>
          </div>
          <br/>
          <b-input-group size="md" prepend="<i class='fa fa-user fa-md'></i>">
            <b-form-input v-model="username" type="text" :placeholder="$t('用户名')"></b-form-input>
          </b-input-group>
          <br/>
          <b-input-group size="md" prepend="<i class='fa fa-lock fa-md'></i>">
            <b-form-input v-model="password" type="password" :placeholder="$t('密码')"></b-form-input>
          </b-input-group>
          <br/>
          <b-input-group size="md">
            <b-button @click="doLogin" size="md" class="form-control btn-success">
              <i class="fa fa-sign-in"></i>
              &nbsp;
              {{$t("登 录")}}
            </b-button>
          </b-input-group>
          <br/>
          <div class="text-danger">
            {{$t(errorMessage)}}
          </div>
        </form>
      </div>
      <div class="col-md-4">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data: function () {
    return {
      username: 'system@lq186.com',
      password: 'lq186#',
      errorMessage: ''
    }
  },
  methods: {
    doLogin: function () {
      let that = this
      if (this.username === '') {
        this.errorMessage = 'Parameter username should not be empty.'
        return
      }
      if (this.password === '') {
        this.errorMessage = 'Parameter password should not be empty.'
        return
      }

      this.$axios.post('/user/login', JSON.stringify({
        username: that.username,
        password: that.password
      })).then(response => {
        if (that.$response.Code.Success === response.Code) {
          that.$store.state.loginInfo = response.Data
          that.$store.commit('loginInfo')
          that.$router.push('/')
        } else {
          that.errorMessage = response.Message
        }
      }).catch(err => {
        that.errorMessage = err.Error
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main-container {
    margin-top: -5rem;
    margin-bottom: -4rem;
    min-height: calc(100% - 20rem);
    width: 100%;
    max-width: 100%;
    padding-top: 10rem;
    padding-bottom: 10rem;
    background-image: url('../assets/images/login-bg.jpeg');
    color: #ffffff;
    animation-name: changebg;
    animation-duration:30s; /*变换时间*/
    animation-delay:0s; /*动画开始时间*/
    animation-iteration-count:infinite; /*下一周期循环播放*/
    animation-play-state:running; /*动画开始运行*/
    background-size: 100% auto;
  }
  .input-group-text {
    width: 2.5rem;
  }
  form {
    padding: 3rem;
    background-color: rgba(0, 0, 0, 0.6);
    border-radius: 5px;
    max-width: 30rem;
    display: block;
    margin: 0 auto;
  }
  .row {
    margin-left: 0px;
    margin-right: 0px;
  }
</style>



