<template>
  <div class="c-nav c-color-nav fixed-top">
    <b-navbar toggleable="md" type="dark" variant="dark">

    <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

    <b-navbar-brand href="#/">
      <img class="logo" src="../assets/images/logo.png" alt="">
      <span class="brand">{{$t("title")}}</span>
    </b-navbar-brand>

    <b-collapse is-nav id="nav_collapse">

      <b-navbar-nav>
        <li class="nav-item">
          <router-link to="/java" class="nav-bg nav-link">
            {{$t("Java")}}
          </router-link>
        </li>
        <li class="nav-item">
          <router-link to="/go" class="nav-bg nav-link">
            {{$t("Go")}}
          </router-link>
        </li>
        <li class="nav-item">
          <router-link to="/nodejs" class="nav-bg nav-link">
            {{$t("NodeJs")}}
          </router-link>
        </li>
      </b-navbar-nav>

      <!-- Right aligned nav items -->
      <b-navbar-nav class="ml-auto">

        <li class="nav-item">
          <router-link v-if="!isLogined" to="/login" class="nav-link">
            {{$t("登录")}}
          </router-link>
          <b-nav-item-dropdown v-else right>
            <template slot="button-content">
              <i class="fa fa-user"></i>
              &nbsp;
              {{loginInfo.Nickname}}
            </template>
            <b-dropdown-item @click="logout()">
              <i class="fa fa-sign-out"></i>
              &nbsp;
              {{$t('退出登录')}}
            </b-dropdown-item>
          </b-nav-item-dropdown>
        </li>

        <b-nav-item-dropdown right>
          <template slot="button-content">
            <img :src="$t('langPng')" :alt="$t('langName')">
            <i class="fa fa-angle-down"></i>
          </template>
          <b-dropdown-item @click="changeLang('zh-CN')">
            <img src="../assets/images/zh-CN.png" alt="简体中文" />
            简体中文
          </b-dropdown-item>
          <b-dropdown-item @click="changeLang('en')">
            <img src="../assets/images/en.png" alt="English" />
            English
          </b-dropdown-item>
        </b-nav-item-dropdown>

      </b-navbar-nav>

    </b-collapse>
  </b-navbar>
  <div class="clear"></div>
</div>
</template>

<script>
import Util from '../assets/js/util'
export default {
  name: 'Nav',
  methods: {
    changeLang: function (lang) {
      this.$i18n.locale = lang
      document.title = this.$t('title')
    }
  },
  mounted () {
    document.title = this.$t('title')
    this.$store.state.loginInfo = Util.getLoginInfo()
    this.$store.commit('loginInfo')
  },
  computed: {
    loginInfo () {
      return this.$store.state.loginInfo
    },
    isLogined () {
      return this.$store.state.isLogined
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .logo {
    width: 2rem;
    height: 2rem;
  }
  .brand {
    font-size: 80%;
  }
  .c-color-nav {
    background-color: #001f3f;
    color: #ffffff;
    animation-name: changebg;
    animation-duration:30s; /*变换时间*/
    animation-delay:0s; /*动画开始时间*/
    animation-iteration-count:infinite; /*下一周期循环播放*/
    animation-play-state:running; /*动画开始运行*/
    background-size: 100% auto;
  }
  @keyframes changebg {
    0%   {background-color:#001f3f;}
    10%  {background-color:#0074D9;}
    20%  {background-color:#7FDBFF;}
    30%  {background-color:#39CCCC;}
    40%  {background-color:#3D9970;}
    50%  {background-color:#2ECC40;}
    60%  {background-color:#01FF70;}
    70%  {background-color:#FFDC00;}
    80%  {background-color:#FF851B;}
    90%  {background-color:#FF4136;}
    100%  {background-color:#B10DC9;}
  }
</style>



